const SanaBanner = () => {
  const [isMobile, setMobile] = useState(document.body.clientWidth < 760);

  useEffect(() => {
    const onresize = () => {
      setMobile(document.body.clientWidth < 760);
    };
    window.addEventListener("resize", onresize, false);
    return () => {
      window.removeEventListener("resize", onresize, false);
    };
  }, []);

  return useMemo(
    () => (
      <Cbanner>
        <img
          src={isMobile ? MbBgbanner : PcBgbanner}
          alt=""
          width="100%"
          height="auto"
        />
      </Cbanner>
    ),
    [isMobile]
  );
};

const DataCard = ({
  title,
  data,
}: {
  title: string;
  data: Array<ElemetType>;
}) => {
  return (
    <Card>
      <CardTitle>
        <BoldText>{title}</BoldText>
      </CardTitle>
      <CardBody>
        <CardRow>
          {data.slice(0, 2).map(({ label, value }, i) => (
            <>
              <CardColumn key={label}>
                <BoldText>{value}</BoldText>
                <MarginTopText>{label}</MarginTopText>
              </CardColumn>
              {i === 0 && <Divider />}
            </>
          ))}
        </CardRow>
        <HiddenDivider />
        <CardRow>
          {data.slice(2, 4).map(({ label, value }, i) => (
            <>
              <CardColumn key={label}>
                <BoldText>{value}</BoldText>
                <MarginTopText>{label}</MarginTopText>
              </CardColumn>
              {i === 0 && <Divider />}
            </>
          ))}
        </CardRow>
        {data[4] && (
          <>
            <HiddenDivider />
            <CardRow>
              <CardColumn>
                <BoldText>{data[4].value}</BoldText>
                <MarginTopText>{data[4].label}</MarginTopText>
              </CardColumn>
            </CardRow>
          </>
        )}
      </CardBody>
    </Card>
  );
};

