import React from "react";
import styled from "styled-components";

export const Text = styled.span(
  (props: { color?: string; weight?: number; fontSize?: number }) => ({
    display: "inline-block",
    width: "100%",
    fontSize: props.fontSize ?? "14px",
    color: props.color ?? "#666666",
    fontWeight: props.weight ?? 500,
    // margin: "3px 0",
  })
);

export const BoldText = styled(Text)`
  font-size: 22px;
  font-weight: 800;
  color: #333333;
  @media (max-width: 960px) {
    font-size: 18px;
  }
  @media (max-width: 760px) {
    font-size: 14px;
  }
`;

export const TxSpan = styled.span`
  font-size: 16px;
  color: #333333;
  font-weight: 500;
  @media (max-width: 760px) {
    font-size: 14px;
  }
`;

export const ColorText = styled(TxSpan)`
  color: #4f7dfa;
  @media (max-width: 760px) {
    font-size: 15px;
  }
`;
